import styled from 'styled-components';
import { View } from './View';

export const Progress = styled(View).attrs((props) => ({
  className: ['progress', props.className].filter((v) => v !== '').join(' '),
}))`
  background-color: ${(props) => props.theme.light.colors.secondary} !important;

  @media (prefers-color-scheme: dark) {
    background-color: ${(props) => props.theme.dark.colors.secondary} !important;
  }
`;

export const ProgressBar = styled(View).attrs((props) => ({
  className: ['progress-bar', props.className]
    .filter((v) => v !== '')
    .join(' '),
  style: { role: 'progressbar' },
}))`
  background-color: ${(props) => props.theme.light.colors.primary} !important;

  @media (prefers-color-scheme: dark) {
    background-color: ${(props) => props.theme.dark.colors.primary} !important;
  }
`;
